"use client";
import React, { useEffect, useState } from "react";
import { fetchUserData } from "../service/UserService";

function MyAccount() {
  const [userData, setUserData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    async function getUserData() {
      try {
        const data = await fetchUserData(); // Assuming this method fetches user data
        setUserData(data);
      } catch (err) {
        setError("Failed to load user data. Please try again later.");
      } finally {
        setLoading(false);
      }
    }
    getUserData();
  }, []);
   const sampleUserData = {
    profilePicture: "https://via.placeholder.com/150",
    name: "John Doe",
    email: "johndoe@example.com",
  };
  setUserData(sampleUserData);
  if (loading) {
    return (
      <div className="flex justify-center items-center min-h-screen">
        <div className="loader ease-linear rounded-full border-4 border-t-4 border-gray-200 h-12 w-12"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="container mx-auto p-4 sm:p-8">
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded relative" role="alert">
          <strong className="font-bold">Error!</strong>
          <span className="block sm:inline"> {error}</span>
        </div>
      </div>
    );
  }

  return (
    <div className="container mx-auto p-4 sm:p-8">
      <div className="bg-white shadow-md rounded-lg overflow-hidden">
        <div className="p-6 bg-gradient-to-r from-blue-500 to-blue-700 text-white flex flex-col sm:flex-row items-center sm:justify-between">
          <div className="flex items-center">
            <img
              src={userData.profilePicture}
              alt="Profile"
              className="w-24 h-24 rounded-full border-4 border-white sm:w-32 sm:h-32"
            />
            <div className="ml-4">
              <h2 className="text-2xl font-semibold">{userData.name}</h2>
              <p className="text-sm sm:text-base">{userData.email}</p>
            </div>
          </div>
          <div className="mt-4 sm:mt-0">
            <button className="bg-orange-500 hover:bg-orange-600 text-white font-semibold py-2 px-4 rounded">
              Edit Profile
            </button>
          </div>
        </div>
        <div className="p-6">
          <h3 className="text-lg font-semibold mb-4">Account Options</h3>
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
            <OptionCard title="Orders" icon="📦" />
            <OptionCard title="Wishlist" icon="❤️" />
            <OptionCard title="Change Password" icon="🔒" />
            <OptionCard title="Refund Policy" icon="💵" />
            <OptionCard title="Return Policy" icon="🔄" />
            <OptionCard title="Delete Account" icon="🗑️" />
          </div>
        </div>
      </div>
    </div>
  );
}

function OptionCard({ title, icon }) {
  return (
    <div className="flex items-center bg-gray-100 p-4 rounded-lg shadow hover:bg-gray-200 transition">
      <div className="text-3xl mr-4">{icon}</div>
      <div className="text-lg font-medium">{title}</div>
    </div>
  );
}

export default MyAccount;
